.properties-container {
    width: 25%;
    /*padding-top: 8px;*/
    height: 100%;

    overflow: auto;
    scroll-margin: 10px;

    background-color: #ffffff;

    border-left: 1px solid rgba(14,19,24,.15);
}

.hideSettings .properties-container
{
    width: 0;
    display: none;
}

.properties-container:hover::-webkit-scrollbar, .properties-container:hover::-webkit-scrollbar-track, .properties-container:hover::-webkit-scrollbar-thumb {
    visibility: visible;
}

.properties-container::-webkit-scrollbar
{
    visibility: hidden;
}

.properties-container::-webkit-scrollbar-track
{
    width: 16px;
    background-color: transparent;

    visibility: hidden;
}

.properties-container::-webkit-scrollbar-thumb
{
    width: 8px;
    background-color: rgba(236,239,241,1);
    border: 4px solid #FFFFFF;
    border-radius: 8px !important;

    visibility: hidden;
}

.properties-container .properties {
    padding: 16px;
    display: none;
}

.properties-container .properties.show {
    display: block;
}

.properties-container .properties.slide {
    padding: 15px 30px; /*todo*/
}

/* slide-properties */

.properties-container .property-row {
    height: 48px;
    padding: 8px 0;
    line-height: 32px;
}

label.template-label {
    font-weight: 700;
}

